
import draggable from "vuedraggable";
import CommFn from "../common-function";
import OnDrop from "../meta/widget-on-drop";

export default {
  components: { draggable },
  props: {
    mainCmp: { required: true },
    widgetDef: { type: Array, required: true },
  },
  methods: {
    /**
     * WidgetDefinition 转换为 RenderedMeta
     */
    onWidgetDrop(def: WidgetDefinition): RenderedMeta {
      console.log("WidgetDefinition 转换为 RenderedMeta", def);

      let _def: RenderedMeta = {
        ...def,
        uid: CommFn.getUid(),
      };

      let d: WidgetDefinition = <WidgetDefinition>(<unknown>_def);
      delete d.eng_name;
      delete d.icon;
      delete d.isHtmlTag;

      let fn: Function | null = OnDrop[def.type];
      fn && fn.call(this, _def);

      // if (def.type == "Row") {
      //   // 特定控件的某些扩展，复合控件
      //   _def.children = [
      //     {
      //       type: "Col",
      //       name: "栅格列容器",
      //       uid: CommFn.getUid(),
      //       children: [],
      //     },
      //     {
      //       type: "Col",
      //       name: "栅格列容器",
      //       uid: CommFn.getUid(),
      //       children: [],
      //     },
      //   ];
      // }
      // if (def.type == "Tabs") {
      //   // 特定控件的某些扩展，复合控件
      //   _def.children = [
      //     {
      //       type: "TabPane",
      //       name: "TabPane",
      //       uid: CommFn.getUid(),
      //       props: {
      //         label: "Tab 1",
      //       },
      //       children: [],
      //     },
      //     {
      //       type: "TabPane",
      //       name: "TabPane",
      //       uid: CommFn.getUid(),
      //       props: {
      //         label: "Tab 2",
      //       },
      //       children: [],
      //     },
      //   ];
      // }

      return _def;
    },
  },
};
